.App-header {
  color: #fff;
  background-color: navy;
  align-items: center;
  padding: 1rem;
}
.list {
  border-bottom: 1px solid #757575;
  padding: 2%;
  color: #333;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.like {
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
}
.txt {
  margin-right: 3rem;
}
.list h5 {
  font-size: 18px;
}
.list p {
  font-size: 12px;
}
button {
  border: none;
  border-radius: 5px;
  padding: 0.5rem 1rem;
  background-color: #ddd;
  color: #333;
  font-weight: 600;
  margin: 1rem;
  cursor: pointer;
}
.modal {
  border: 2px solid #cccccc;
  border-radius: 20px;
  padding: 1rem;
  background-color: #ffffff;
  box-shadow: 4px 4px 4px #d9d9d9;
  width: 300px;
  height: 180px;
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -50%);
}
